.app-container {
  background-color: cornsilk;
}

.image-container {
  transition: transform 0.2s;
  border-style: none;
}

.image-container:hover {
  cursor: pointer;
  transform: scale(1.1);
}

.image-container:hover .overlay {
  opacity: 1;
}

.overlay {
  position: absolute;
  width: 100%;
  transition: 0.5s ease;
  opacity: 0;
  bottom: 0;
  font-size: 10px;
  padding: 20px;
  text-align: center;
}

.fav-btn {
  padding-left: 35px;
}